/* Animations */

/* Enter */

.review-enter {
    /* visibility: hidden; */
    opacity: 0;
    /* z-index: 1; */
  }
  
  .review-enter.review-enter-active {
    /* visibility: visible; */
    opacity: 1;
    /* transition: opacity 500ms linear; */
    -webkit-animation: rmovein 300ms;
            animation: rmovein 300ms;
  }
  
  /* Exit */
  
  .review-exit {
    /* visibility: hidden; */
    opacity: 1;
  }
  
  .review-exit.review-exit-active {
    /* visibility: hidden; */
    opacity: 0;
    /* transition: opacity 500ms linear; */
    -webkit-animation: rmoveout 300ms;
            animation: rmoveout 300ms;
  }
  
  .review-exit-done {
    /* visibility: hidden; */
    opacity: 0;
  }
  
  /* Animations */
  
  @-webkit-keyframes rmovein {

    from {
      opacity: 0;
    }

    to{
      opacity: 1;
    }
  }
  
  @keyframes rmovein {

    from {
      opacity: 0;
    }

    to{
      opacity: 1;
    }
  }
  
  /* Reverese movein */
  @-webkit-keyframes rmoveout {

    from{
      opacity: 1;
    }

    to{
      opacity: 0;
    }
  }
  @keyframes rmoveout {

    from{
      opacity: 1;
    }

    to{
      opacity: 0;
    }
  }


