/* Enter */

.fade-enter {
  /* visibility: hidden; */
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  /* visibility: visible; */
  opacity: 1;
  /* transition: opacity 500ms linear; */
  -webkit-animation: movein 300ms ease forwards;
          animation: movein 300ms ease forwards;
  will-change: transform;
}

/* Exit */

.fade-exit {
  /* visibility: hidden; */
  opacity: 1;
}

.fade-exit.fade-exit-active {
  /* visibility: hidden; */
  opacity: 0;
  /* transition: opacity 500ms linear; */
  -webkit-animation: moveout 200ms ease forwards;
          animation: moveout 200ms ease forwards; 
  will-change: transform;
}

.fade-exit-done {
  /* visibility: hidden; */
  opacity: 0;
}

/* Animations */

@-webkit-keyframes movein {
  0% { 
    -webkit-transform: translateY(0) scale3d(0.8,0.8,1); 
            transform: translateY(0) scale3d(0.8,0.8,1); 
    opacity: .25;
  }
  20% { 
    -webkit-transform: scale3d(1.05,1.05,1); 
            transform: scale3d(1.05,1.05,1); 
    opacity: .5;
  }
  40% { 
    opacity: .75;
  }
  100% {
    opacity: 1;
  }
}

@keyframes movein {
  0% { 
    -webkit-transform: translateY(0) scale3d(0.8,0.8,1); 
            transform: translateY(0) scale3d(0.8,0.8,1); 
    opacity: .25;
  }
  20% { 
    -webkit-transform: scale3d(1.05,1.05,1); 
            transform: scale3d(1.05,1.05,1); 
    opacity: .5;
  }
  40% { 
    opacity: .75;
  }
  100% {
    opacity: 1;
  }
}

/* Reverese movein */
@-webkit-keyframes moveout {


  0% {  
    opacity:1;
  }
  20% {  
    -webkit-transform: scale3d(1.01,1.01.1,1);  
            transform: scale3d(1.01,1.01.1,1); 
    opacity: .75;
  }
  40% { 
    opacity: .5;
  }

  100% {
    -webkit-transform: scale3d(0.6,0.6,1);
            transform: scale3d(0.6,0.6,1);
    opacity: 0;
  }
}
@keyframes moveout {


  0% {  
    opacity:1;
  }
  20% {  
    -webkit-transform: scale3d(1.01,1.01.1,1);  
            transform: scale3d(1.01,1.01.1,1); 
    opacity: .75;
  }
  40% { 
    opacity: .5;
  }

  100% {
    -webkit-transform: scale3d(0.6,0.6,1);
            transform: scale3d(0.6,0.6,1);
    opacity: 0;
  }
}

@-webkit-keyframes shake {
  0%{
    -webkit-transform: translatex(10px);
            transform: translatex(10px);
  }
  25%{
    -webkit-transform: translatex(-10px);
            transform: translatex(-10px);
  }
}

@keyframes shake {
  0%{
    -webkit-transform: translatex(10px);
            transform: translatex(10px);
  }
  25%{
    -webkit-transform: translatex(-10px);
            transform: translatex(-10px);
  }
}





