@-webkit-keyframes bouncing-loader {
    to {
      opacity: .1;
      -webkit-transform: translate3d(0, -1rem, 0);
              transform: translate3d(0, -1rem, 0);
    }
  }

@keyframes bouncing-loader {
    to {
      opacity: .1;
      -webkit-transform: translate3d(0, -1rem, 0);
              transform: translate3d(0, -1rem, 0);
    }
  }
  .bouncing-loader {
    z-index: 1;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
  }
  .bouncing-loader > div {
    z-index: 1;
    width: 1rem;
    height: 1rem;
    margin: 3rem 0.2rem;
    background: #0CA5B0;
    border-radius: 50%;
    -webkit-animation: bouncing-loader .6s infinite alternate;
            animation: bouncing-loader .6s infinite alternate;
  }
  .bouncing-loader > div:nth-child(2) {
    z-index: 1;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
  }
  .bouncing-loader > div:nth-child(3) {
    z-index: 1;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
  }

  @-webkit-keyframes bouncing-loader-white {
    from {
        opacity: 1;
        -webkit-transform: translate3d(0, -.9rem, 0);
                transform: translate3d(0, -.9rem, 0);
    }
    to {
      opacity: 0;
      /* transform: translate3d(0, -1rem, 0); */
    }
  }

  @keyframes bouncing-loader-white {
    from {
        opacity: 1;
        -webkit-transform: translate3d(0, -.9rem, 0);
                transform: translate3d(0, -.9rem, 0);
    }
    to {
      opacity: 0;
      /* transform: translate3d(0, -1rem, 0); */
    }
  }
  .bouncing-loader-white {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    z-index: 2;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
  }

  .bouncing-loader-white > div {
    z-index: 2;
    width: 1rem;
    height: 1rem;
    margin: 3rem 0.2rem;
    background: #eeeeee;
    border-radius: 50%;
    animation: bouncing-loader-white .7s infinite alternate-reverse;
  }
  .bouncing-loader-white > div:nth-child(2) {
    z-index: 2;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
  }
  .bouncing-loader-white > div:nth-child(3) {
    z-index: 2;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
  }
