    width: 25%;
    height: 300px;
    /* background-color: antiquewhite; */
    font-family: 'Krungthep';
    text-align: center;
    background-color: #282f3a;
    border-radius: 0px 3px 3px 0px;
    /* box-shadow: 2px 0px 5px #232a33; */
}


.summoner-rank{
    color: #00adb5;
}

.column2 {
    width: 25%;
    height: 300px;
