    /* background-color: red; */
}

.summonerImage{
    background-size: 120px;
    width:120px;
    height:120px;

    display: block;
    margin: 60px auto 0px auto;

    border-style: solid;
    border-width: 2px;
    border-color: #998a05;
    border-radius: 3px;
    box-shadow: 0px 0px 40px #000000;
}

