    box-shadow: 0px 0px 20px #998a05;
} */

.summoner-label{
    color: #eeeeee;
}

.favorite-player{
    margin-left: 10px;
    color: #FE4365;
    font-style: normal;
    cursor: pointer;
}


.game-summary-container{
    width: 25%; 
    height: 200px;
    float: left;
    margin: 20px 0px 0px 20%;
