    font-family: 'Krungthep';
}

.wr-kda-container{
    width: 100%;
    height: 100px;
    display: flex;
}

.wr-container {
    width: 100%;
    display: flex;
}

.kda-box-container {
    background-color: #222a32;
    width: 100%;
    display: flex;
}
.wr-box, .kda-box {
    color: #eeeeee;
    font-size: .7vw;
    text-align: center;
    vertical-align: middle;
    /* line-height: 25px; */
    margin: auto 0px auto 20px;
}
.kda-box{
    margin: auto 0px auto 10px;
}
.wr-header{
    font-size: 19px;
}

.wr-blue{
    color: #0CA5B0;
}

.wr-red{
    color: #FE4365;
}

.wr-piegraph {
    height: 100%;
    width: 75px;
    margin-left: 10px;
}

.kda-piegraph {
    height: 100%;
    width: 75px;
    margin-left: 10px;
}

.top-champion-container{
    width: 100%;
    height: 150px;
    background-color: #212830;
    box-shadow: 0 8px 6px -7px black;
}
.champion-cell {
    width: 90%;
    height: 40px;
    margin: 0px 5% 5px 5%;
    display: flex;
    justify-content: space-between;
    text-align: center;
}

.champion-cell-image{
    width: 40px;
    height: 40px;
    background-size: 40px;
    margin: 0px 0px 0px 0px;
    float: left;
}

.champion-cell-name {
    font-family: 'Krungthep';
    font-size: 10px;
    color: #eeeeee;
    line-height: 40px;
    margin-left: 5px;
    float: left;
    text-align: center;
}

.wr-box {
    /* float: right; */
    /* margin-right: 20px; */
}

.kda-box {
    /* float: right; */
    /* margin-right: 5px; */
}

.top-champions {
    width: 100%;
    height:130px;
    /* background-color: blue; */
    padding-top: 10px;
}
.match-cell {
    width: 100%;
    height: 90px;
    background-color: #272f39;
    margin-bottom: 10px;
    border-radius: 3px;
    box-shadow: 0px 8px 12px -8px black;

    display: flex;

    transition: all .2s;
}

.match-cell:hover {
    -webkit-transform: scale3d(1.03, 1.03, 1);
            transform: scale3d(1.03, 1.03, 1);
    /* box-shadow: 0px 0px 10px .1px black; */
}

.match-cell:hover .result-text-victory{
    color: #00adb5;
    text-shadow: 0px 0px 1px #00adb5;
}

.match-cell:hover .result-text-defeat{
    color: #ff2e63;
    text-shadow: 0px 0px 1px #ff2e63;
}

.result-bar {
    height: 100%;
    width: 10px;
} 

.result-bar-victory {
    background-color: #00adb5;

} 

.result-text-victory {
    color: #00acb5bb;
    transition: color 1s;
}

.result-bar-defeat {
    background-color: #ff2e63;
    
}

.result-text-defeat {
    color: #ff2e62bd; 
    transition: color .5s;
}

.result-status {
    margin: 0px 20px;
    font-family: 'Krungthep';
    font-size: 15px;
    vertical-align: middle;
    line-height: 78px;
    text-align: center;

    transition: color 1s;
}

.champion-container{
    margin-top: 13px;
}

.match-champion-icon{
    width: 50px;
    height: 50px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 55px;

    margin: 0px 5px 5px 0px;

    border-radius: 3px;
}

.match-time{
    font-size: 10px;
    font-family: 'Krungthep';
    color: #eeeeee91;
    z-index: 1;
    text-align: center;
    margin-top: -30px;
}

.match-champion-text{
    width: 100%;
    font-size: 12px;
    font-family: 'Krungthep';
    color: #eeeeee;
    text-align: center;
}

.spell-icon{
    width: 25px;
    height: 25px;
    background-size: 25px;

    background-position: center;
    background-repeat: no-repeat;

    border-radius: 2px;
}

.match-item-icon{
    width: 30px;
    height: 30px;
    background-size: 30px;

    background-position: center;
    background-repeat: no-repeat;

    border-radius: 4px;
}

.match-history-container{
    width: 34.5%;
    margin: 18px 20% 0px .5%;
    float: left;
}

.container {
    margin-top: 100px;
}

.reviewCell {
    width: 100%;
    /* background-color: rgb(12, 160, 170); */
    margin: 2px 0px;
    border-radius: 2px;
    /* box-shadow: 0px 2px #006368;   */
    display: flex;
    flex-wrap: wrap;
    transition: height .2s;
    font-family: 'Krungthep';
}

.cell-open{
    height: 120px;
}

.cell-closed{
    height: 60px;
}

.cell-header{
    display: inline;
    width: 95%;
    height: 10px;
    margin: 3px 2.5% 3px 2.5%;
    font-size: 10px;
}

.cell-summoner{
    font-size: 10px;
    display: inline;
    color: #eeeeee;
}

.cell-close-detail{
    display: inline;
    float: right;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.cell-detail-edit {
    transition: font-size .2s, color .2s;
}

.cell-detail-edit:hover {
    font-size: 14px;
}

.cell-detail-close{
    transition: font-size .2s, color .2s;
    color: #171d21;
    font-size: 15px;
}

.cell-detail-close:hover {
    font-size: 18px;
}

.cell-content{
    border-radius: 2px;
    padding: 2px 4px;
    background-color: transparent;
    border: 0px solid transparent;

    /* height: 10px; */
    /* min-height: 10px; */

    min-width: 50%;
    /* max-width: auto; */
    /* margin: 2px 5%; */ 

    height: 0px;
    resize:vertical;
    color: #171d21;
    font-size:12px;
    line-height:1.5;
    font-weight:300px;
    font-family: 'Krungthep';
    margin: 5px 15px;
    float: top;

    transition: height .15s;
    resize: none;
}

.cell-content-closed{
    height: 18px;
    width: 250px;
    max-height: 18px;
    box-shadow: none;
}

.cell-content-open{
    height: 86px;
    width: 50%;
    max-height:86px;
    box-shadow: inset 0px 0px 2px .3px black;
}

.cell-content-closed:focus, .cell-content-open:focus{
    outline: none !important;
    border: none;

    /* 
        max-height: 50px;
        max-width: 100%;
    */
}

.review-cell-rating1,.review-cell-rating2, .review-cell-rating3 , .review-cell-rating4 , .review-cell-rating5{
    font-family: 'Krungthep';
    font-size: 25px;
    margin-right: 40px;
    color: #eeeeee;
}

.cell-ratings {
    margin: 5px 0px;
    text-align: center;
    font-size: 20px;
    letter-spacing: 10px;
    cursor: pointer;
}


.cell-rating-Selected{
    color: #eeeeee;
    font-size: 27px;
    cursor: pointer;
}


.cell-rating:hover{
    font-size: 27px;
    transition: font-size .2s, color .2s;
}

.cell-edit-right{
    width: 50%;
    margin: 5px 15px;
}


.cell-button-container{

    height: 30px;
    margin: 10px 0px 0px 0px;
    display: flex;
    justify-content: center;
    font-size: 10px;
}

.cell-save-button{
    background-color: #171d21;
    width: 45px;
    height: 30px;
    margin-right: 18px;
    margin-bottom: 2px;
    cursor: pointer;

    border-radius: 3px;
    box-shadow: 0 0px 18px 1px rgba(0, 0, 0, 0.486);

    text-align: center;
    color: #12a0aa;
    vertical-align: middle;
    line-height: 30px;

    transition: all .2s;

}

.cell-save-button:hover{
    text-shadow: 0px 0px 3px #12a0aa;
}

.cell-save-button:active, .cell-delete-button:active{
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}

.cell-delete-button{
    background-color: #171d21;
    width: 45px;
    height: 30px;
    margin-left: 18px;
    cursor: pointer;

    border-radius: 3px;
    box-shadow: 0 0px 18px 1px rgba(0, 0, 0, 0.486);
    /* box-shadow: 0px 2px #eeeeee; */

    text-align: center;
    color: #d12828;
    vertical-align: middle;
    line-height: 30px;
}

.cell-delete-button:hover{
    text-shadow: 0px 0px 3px #d12828;
}



.favorite-cell{
    width: 85%;
    margin: 0px 7.5% 10px 7.5%;
    background-color: transparent;

    display: flex;
    /* margin-left: auto; */

    transition: all .2s;

    font-family: 'Krungthep';

}

.favorite-cell:hover{
    -webkit-transform: scale3d(1.1,1.1,1);
            transform: scale3d(1.1,1.1,1);
    box-shadow: 0px 0px 10px 3px black;
}

.favorite-icon{
    width: 40px;
    height: 40px;
    background-size: 40px;
    transition: all .2s;
}

.favorite-summoner{
    color: #eeeeee;
    font-size: 10px;
    margin: 5px;
    float: left;
}

.favorite-remove{
    visibility: hidden;
}

.favorite-cell:hover .favorite-remove{
    /* float: right; */
    visibility: visible;
    font-size: 10px;
    margin: 5px 10px 0px auto;
    text-align: center;
    vertical-align: middle;
    line-height: 30px;
    color: crimson;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.favorite-remove:hover{
    text-shadow: 0px 0px 3px crimson;
}
