.review-cell{
    background-color: #00adb5;
    height: 60px;
    width: 90%;
    margin: 5px 5% 15px 5%;
    border-radius: 4px;
    border: 2px solid;
    box-shadow: 0px 2px #006368;
    display: flex;
    justify-content: space-between;
}

.reviewer-text {
    display: inline-block;
    height: 15px;
    font-size: 15px;
    width: 100%;
    margin: 0px 0px 0px 5px;
    font-family: 'Krungthep'; 
    color: #eeeeee;
    text-shadow: 0px 1px black;
}

.review-content {
    z-index: 1;
    /* display: inline-block; */
    margin-left: -85%;
    margin-top: 5px;
    font-family: 'Krungthep';
    font-size: 12px;
    color: #212830;
    /* margin:10px 5% 0px 0px; */
    /* margin-left: auto; */
    /* text-align: center; */
    vertical-align: middle;
    line-height: 60px;
    word-wrap: break-word;
    width: 175px;
    /* display: flex;
    flex-wrap: wrap;  */
    justify-content: center; 
    text-align: center;
}

.review-content > em {
    text-shadow: 0px 1px black;
    display: inline-block;
    vertical-align: middle;
    line-height: 60px;
    word-wrap: break-word;
}

.cell-rating1,.cell-rating2, .cell-rating3 , .cell-rating4 , .cell-rating5, .cell-rating6{
    font-family: 'Krungthep';
    font-size: 40px;
    width: 10%;
    float: right;
    margin: 5px 15px 5px 0px;
    text-align: right;
    color: #eeeeee;
}

